import { useEffect, useState, Fragment} from 'react';
import { useParams } from 'react-router-dom';

import MainLayout from '../../components/layout/mainLayout/Main';
import Layout from '../../components/layout/componentLayout/Layout';
import Loader from '../../components/Loader/loader';
import ErrorBar from '../../components/errorbar/Errorbar';
import TableInfo from './tableInfo/tableinfo';
import Network from '../../components/network/network';
import classes from './GoTerm.module.css'
import AButton from '../../components/button/aButton';

const GoTerm = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [isError, setIsError] = useState(false);

  const [label, setLabel] = useState([]);
  const [featuresType, setFeaturesType] = useState([]);
  const [features, setFeatures] = useState([]);
  const [featuresDescription, setFeaturesDescription] = useState([]);
  const [scores, setScores] = useState([]);
  const [modeLink, setModelLink] = useState([]);
  const [network, setNetwork] = useState(null);
  const [searchDescription, setSearchDescription] = useState(null);
  const [OOBF1,setOOBF1] = useState(null);
  const [startNode, setStartNode] = useState([]);
  const [endNode, setEndtNode] = useState([]);
 
  const params = useParams();
  const goId = params.goId.replace(/\s+/g, '');
  window.scrollTo(0, 0);

  // Get data from database
  useEffect(() => {
    const data = { labels: goId };

    fetch('https://go-label-316405.oa.r.appspot.com/api/go', {
      method: 'POST',
      body: JSON.stringify(data),
      headers: {
        'content-type': 'application/json',
      },
    })
      .then((response) => {
        console.log(!response.ok);
        if (!response.ok) {
          throw new Error(response.status);
        }

        return response.json();
      })
      .then((data) => {
        setFeaturesType(data.featuresTypes);
        setFeatures(data.features);
        setFeaturesDescription(data.featuresDesciption);
        setScores(data.scores);
        setLabel(data.terms);
        setModelLink(data.modelLink);
        setIsLoading(false);
        setSearchDescription(data.termsDescription);
        setOOBF1(data.model.oob_f1);
        setStartNode(data.network.start);
        setEndtNode(data.network.end);

        setNetwork(
          [
         ]
        )
      })
      .catch((error) => {
        setIsError(true);
        setIsLoading(false);
        setLabel(['Error']);
        setScores(['Error']);
        setFeatures(['Error']);
      });
  }, [goId]);


  return (
    <MainLayout>
      {isLoading && <Loader />}
      {isError && <ErrorBar />}

      
      <Layout>
          <div className={classes.goDiv}>
            <p className={classes.term}>Term:</p>
            <h1>{label}</h1>
            <p>Description: {searchDescription}</p>
            <p>OOB F1: {OOBF1}</p>
            <br/>
            <AButton href={modeLink[0]}>Download Model</AButton>
          </div>
          <br/>
          <br/>

          { (network !== null) &&

              <Fragment>
              <h2 className={classes.networkH2}>Network</h2>
                <Network 
                  start={startNode} 
                  end={endNode}
                  />
              </Fragment>
            
          }
      </Layout>

      {Array.from({ length: label.length }, (_, i) => i).map((index) => {

        return (
          <TableInfo
            indexes = {index}
            label = {label}
            features = {features}
            featuresType = {featuresType}
            featuresDescription = {featuresDescription}
            scores = {scores}
            modelLink = {modeLink[0]}
          />
        );
      })}
    </MainLayout>
  );
};

export default GoTerm;
