import React, { useEffect, useState } from 'react';
import CytoscapeComponent from 'react-cytoscapejs';
import AButton from '../button/aButton';
import classes from './network.module.css';

const Network = (props) => {
  let [sizeHeight, setSizeHeight] = useState('300px');
  let [increaseable, setIncreaseable] = useState(true);
  let [decreaseable, setDecreaseable] = useState(false);
  let [element, setElement] = useState([]);

  useEffect(() => {
    let dumpElement = []
    
    for(let i = 0; i < props.start.length; i++) {
      let startNode = props.start[i];
      let endNode = props.end[i];
      dumpElement.push({ data: { id: startNode, label: startNode}, 
                        position: { x: Math.floor(Math.random() * 2001), y: Math.floor(Math.random() * 2001) }});
      dumpElement.push({ data: { id: endNode, label: endNode}, position: { x: Math.floor(Math.random() * 2001), y: Math.floor(Math.random() * 2001) }});
      dumpElement.push({ data: { source: startNode, target: endNode, label: 'Edge' } });
    }
  
    setElement(dumpElement);
    console.log(dumpElement);
  }, []);

  const layout = {
    name: 'random'
  }

    const stylesheet = [
      {
        selector: 'node',
        style: {
          width: 18,
          height: 18,
          opacity: 0.8,
          label: 'data(label)',
          backgroundColor: '#57837B',
        }
      },
      {
        selector: 'edge',
        style: {
          opacity: 0.5,
          lineColor: '#515E63'
        }
      }
    ];
    
    const increasehandler = () => {
      setSizeHeight(prevHeight => {
        setIncreaseable(true);
        setDecreaseable(true);
        let number = parseInt(prevHeight, 10) + 100;

        if (number === 300) {
          setDecreaseable(false);
        };

        return number+'px'
      });
    }

    const decreasehandler = () => {
      setSizeHeight(prevHeight => {
        setIncreaseable(true);
        setDecreaseable(true);

        let number = parseInt(prevHeight, 10) - 100;

        if (number === 300) {
          setDecreaseable(false);
        };
        return number+'px'
      });
    }

    

    return(
        <div className={classes.network}>
          <CytoscapeComponent 
              elements={element} 
              style={ {  
                height: sizeHeight,
              } } 
              stylesheet={stylesheet}
              layout={layout} 
              pan={ { x: 0, y: 0 } }
              zoom={0.8}
              minZoom={0.5}
              maxZoom={2}
              />

            <AButton 
              className={classes.increase} 
              onClick={increasehandler}
              disable={!increaseable}>➔</AButton>
            <AButton 
              className={classes.decrease} 
              onClick={decreasehandler}
              disable={!decreaseable}>➔</AButton>
        </div>    
        )
}
export default Network;